.section-title-main {
  text-align: center;
  border: 1px solid purple;
  padding: 3px 10px;
  border-radius: 20px;
  margin: 0;
}

.image-container {
  color: purple;
  border: 10px solid purple;
  border-image: repeating-linear-gradient(
      45deg,
      pink,
      pink 1%,
      purple 1%,
      purple 8%
    )
    10;
}

.image-container:hover {
  border-image: repeating-linear-gradient(
      45deg,
      purple,
      purple 1%,
      pink 1%,
      pink 8%
    )
    10;
  color: purple;
}

.image-container-2 {
  border: 10px solid transparent;
  border-image: url(../assets/other/border.png) 30 round;
}

.purple-border {
  -webkit-filter: drop-shadow(3px 3px 0 purple) drop-shadow(-3px -3px 0 purple);
  filter: drop-shadow(3px 3px 0 purple) drop-shadow(-3px -3px 0 purple);
}

#overlay {
  position: fixed;
  display: block;
  transition: 2s ease-in;
  width: 70%;
  height: 80%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  cursor: pointer;
}
